@import"https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@300;500;700&family=Orbitron:wght@400;700&display=swap";:root{--erotic-pink: #ff0077;--pink-glow: 0 0 8px rgba(255, 0, 119, 0.5);--detective-cyan: #00ffff;--cyan-glow: 0 0 8px rgba(0, 255, 255, 0.5);--bg-dark: #10001a;--bg-panel: #2a0a3a;--border-color: rgba(255, 0, 119, 0.3);--text-light: #f5f5f5;--text-dim: #b0a0c0;--font-main: 'Noto Sans SC', sans-serif;--font-hud: 'Orbitron', sans-serif}body{background-color:var(--bg-dark);color:var(--text-light);font-family:var(--font-main);display:flex;justify-content:center;align-items:center;min-height:100vh;padding:15px;margin:0;box-sizing:border-box}.character-creation-container{background:rgba(0,0,0,0);border:none;border-radius:8px;padding:20px;max-width:900px;width:100%;box-shadow:0 0 20px rgba(255,0,119,.2),0 4px 30px rgba(0,0,0,.5);position:relative;overflow:hidden;clip-path:polygon(0 10px, 10px 0, calc(100% - 10px) 0, 100% 10px, 100% calc(100% - 10px), calc(100% - 10px) 100%, 10px 100%, 0 calc(100% - 10px))}.character-creation-container::before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background-size:30px 30px;opacity:.3;pointer-events:none}.main-title{font-family:var(--font-hud);color:var(--erotic-pink);text-shadow:var(--pink-glow);text-align:center;font-size:2.2em;margin-top:0;margin-bottom:15px}.intro-text{color:var(--text-dim);text-align:center;font-size:.95em}.intro-text h2{font-family:var(--font-main);font-weight:500;font-size:1.1em;margin-top:20px;color:var(--erotic-pink);text-shadow:var(--pink-glow)}.selection-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));gap:15px}.hud-block{background:rgba(0,0,0,.3);border:1px solid rgba(0,255,255,.2);padding:15px;border-radius:5px;margin-bottom:20px}.hud-block-title{font-family:var(--font-hud);color:var(--detective-cyan);font-size:1rem;margin-top:0;margin-bottom:15px;text-transform:uppercase;letter-spacing:1.5px;border-bottom:1px dashed rgba(0,255,255,.4);padding-bottom:8px}.option-card{background:rgba(0,0,0,.2);border:1px solid rgba(0,255,255,.3);padding:15px;border-radius:4px;cursor:pointer;transition:all .3s ease;display:flex;flex-direction:column}.option-card:hover{border-color:var(--detective-cyan);background:rgba(0,255,255,.05)}.option-card.selected{border-color:var(--erotic-pink);background:rgba(255,0,119,.1);box-shadow:var(--pink-glow)}.option-title{font-weight:700;color:var(--text-light);margin-bottom:8px;font-size:1em}.option-description{font-size:.9em;color:var(--text-dim);line-height:1.5;flex-grow:1}.confirm-button-container{text-align:center;margin-top:25px}.confirm-button{font-family:var(--font-hud);background:rgba(0,0,0,0);border:2px solid var(--erotic-pink);color:var(--erotic-pink);padding:12px 35px;font-size:1.1em;cursor:pointer;border-radius:4px;transition:all .3s ease;text-shadow:var(--pink-glow);width:100%;max-width:300px}.confirm-button:hover:not(:disabled){background:var(--erotic-pink);color:var(--bg-dark);box-shadow:0 0 15px var(--erotic-pink)}.confirm-button:disabled{border-color:#555;color:#555;cursor:not-allowed;text-shadow:none}@media(max-width: 768px){body{padding:10px;align-items:flex-start}.character-creation-container{padding:15px;clip-path:none;border-radius:8px}.main-title{font-size:1.8em}.intro-text p{font-size:.9em;line-height:1.6}.hud-block{padding:12px}.selection-grid{grid-template-columns:1fr;gap:12px}.option-card{padding:12px}.confirm-button-container{margin-top:20px}.confirm-button{padding:14px 20px;font-size:1em}}